
// ** React Imports
import { Fragment, useState, useEffect } from 'react'
import Swal from 'sweetalert2'
import UiLoader from '@components/ui-loader'
import classnames from 'classnames'
import Flatpickr from 'react-flatpickr'
import '@styles/react/libs/flatpickr/flatpickr.scss'
// ** Invoice List Sidebar

// ** Columns
// import AddQuiz from '../add'

// ** Store & Actions
import { useForm, Controller } from 'react-hook-form'
import { getcricketTotal, getCricketFixture, getCricketFixtureadd, fixturetoggle, deletefixturecricket, viewfixtureedit, searchCicketFixture, getcricketfixturedownload, getdaterange } from '../../store/actions'
import { useSelector, useDispatch } from 'react-redux'
// import { selectThemeColors } from '@utils'
import Breadcrumbs from '@components/breadcrumbs'
// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown, Edit, Filter, Trash2, HelpCircle } from 'react-feather'
import DataTable from 'react-data-table-component'
import Moment from "moment"
//import UiLoader from '@components/ui-loader'
import { Card, Row, Col, Label, Button, Spinner, CustomInput, Badge, Popover, PopoverHeader, PopoverBody} from 'reactstrap'
import { getAllSeries, getAllFixture, collectseriesid, collectfixtureid, getval } from "../../../Report/CricketReport/store/actions"
// ** Styles
import Select from 'react-select'
import { selectThemeColors } from '@utils'
import withReactContent from 'sweetalert2-react-content'
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { useHistory } from 'react-router-dom'
import AppCollapse from '@components/app-collapse'

// ** Import Scrollbar Paginate Component
import { AsyncPaginate } from "react-select-async-paginate"

// ** Table Header
const CustomHeader = () => {

  return (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-2 mb-75">
     
    </div>
  )
}

const UsersList = () => {

  const defaultValues = {
    reactSelect: null
  }
  const { handleSubmit, control } = useForm({ defaultValues })


  const typeOptions = [
    { value: '1', label: 'ODI' },
    { value: '2', label: 'T20' },
    { value: '3', label: 'TEST' },
    { value: '4', label: '' }
  ]

  const statusOptions = [
    { value: '1', label: 'Live' },
    { value: '2', label: 'Upcoming' },
    { value: '3', label: 'Completed' },
    { value: '4', label: 'Cancelled' }
  ]

  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector((state) => state.fix)
  const store1 = useSelector((state) => state.fix)
  const sto = useSelector((state) => state.rep)
  // ** States
  const [searchTerm, setSearchTerm] = useState('')
  const [popoverOpen, setPopoverOpen] = useState(false)
  const [status, setStatus] = useState(false)
  const [listData, setListData] = useState([])
  const [currentPage, setCurrentPage] = useState(1)
  const [rowsPerPage, setRowsPerPage] = useState(10)
  const [paginate, setPaginate] = useState(0)
  const MySwal = withReactContent(Swal)
  const history = useHistory()
  const [sid, setSid] = useState(0)
  const [fid, setFid] = useState(0)
  const [val, setVal] = useState(false)
  const [serilist, setSerilist] = useState("")
  const [fixlist, setFixlist] = useState("")
  const [refresh, setRefresh] = useState(false)
  const [searchType, setSearchType] = useState('')
  const [searchStatus, setSearchStatus] = useState('')
  const [click, setClick] = useState(false)
  const [page, setPage] = useState(1)
  const [value] = useState(null) 
  // const [date_val, setDate_val] = useState(false)
  const [ccpicker, setCcpicker] = useState(store.cric_from)
  const [ccdateto, setCcdateto] = useState(store.cric_to)

  Moment().format("MMM Do YY")

  // ** Get data on mount
  useEffect(() => {
    // const idea = `{"series_id":${sto.seriesid}, "fixture_id":"${sto.fixtureid}", "page":${currentPage}}`
    const idea = `{"series_id":${sto.seriesid}, "fixture_id":"${sto.getval ? sto.fixtureid : ""}", "page":${currentPage}, "from":"${ccpicker}", "to":"${ccdateto}"}`
    if (store.cricket_add_data.length !== 0) {
      dispatch(getCricketFixtureadd(idea))
    } else {
      dispatch(getCricketFixture(currentPage))
    }
    dispatch(getAllSeries())
  }, [paginate])

  useEffect(() => {
    dispatch(getcricketTotal(page))
  }, [page])

  useEffect(() => {
    if (store.isloading) {
      <Spinner />
      console.log("click")
      console.log(store.isloading)
    } else {
      setListData(store.cricket_data)
      console.log(store.isloading)
      console.log(store)
    }
  }, [store.isloading])

  const download = () => {
    dispatch(getcricketfixturedownload(ccpicker, ccdateto))
  }

  const edit = (date) => {
    setVal(false)
    const local = new Date(date)
    const datestring =  `${local.getFullYear()}-${(local.getMonth()) + 1}-${local.getDate()}`
    setCcdateto(datestring)
    console.log(datestring)
  }

  const edittime = (date) => {
    setVal(false)
    const local = new Date(date)
    const datestring =  `${local.getFullYear()}-${(local.getMonth()) + 1}-${local.getDate()}`
    setCcpicker(datestring)
    console.log(datestring)
  }

  const handleConfirmText = (id) => {
    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ml-1'
      },
      buttonsStyling: false
    }).then(function (result) {
      if (result.value) {
        dispatch(deletefixturecricket(id))
      }
    })
  }
  const editHandler = (row) => {

    console.log(row.size)
    history.push({ pathname: "/cricketfixturesedit" })
    dispatch(viewfixtureedit(row.id))

  }

  const SwitchClick = (row) => {
    setStatus(!status)
    dispatch(fixturetoggle(row, status))

    
  } 

  // const formatDate = (value, formatting = { month: 'short', day: 'numeric', year: 'numeric', hour: 'numeric', minute: 'numeric', second: 'numeric' }) => {
  //   if (!value) return value
  //   return new Intl.DateTimeFormat('en-GB', formatting).format(new Date(value))

  // }
  const statusObj = {
    1: 'light-success',
    2: 'light-warning',
    3: 'light-danger',
    4: 'danger'
  }
  console.log(store.cricket_page.from)

  const columns = [
    {
      name: '#',
      minWidth: '10px',
      selector: 's_no',
      sortable: true,
      cell: (row, pos) => `#${store.cricketSearchFixture?.from + pos}`
    },
    {
      name: 'Fixture Name',
      minWidth: '250px',
      selector: 'fixture_name',
      sortable: true,
      cell: (row) => row.fixture_name

    },
    {
      name: 'Fixture Short Name',
      minWidth: '260px',
      selector: 'fixture_sh',
      sortable: true,
      cell: (row) => (
        // <div>
        <Row>
          <Col>
            <img src={row.team_a_logo} style={{ height: '25px', width: '25px' }} />
          </Col>
          {row.fixture_sh}
          <Col>
            <img src={row.team_b_logo} style={{ height: '25px', width: '25px' }} />
          </Col>
        </Row>
        // </div>
      )

    },
    {
      name: 'Date&Time',
      minWidth: '200px',
      selector: 'date_time',
      sortable: true,
      cell: (row) => Moment((row.date_time), 'DD-MM-YYYY').format("DD MMM YYYY HH:MM:SS")

    },
    {
      name: 'Series Name',
      minWidth: '300px',
      selector: 'series_name',
      sortable: true,
      cell: (row) => row.series_name
    },

    {
      name: 'Contest Type',
      minWidth: '160px',
      selector: 'type',
      sortable: true,
      cell: (row) => row.type

    },
    {
      name: 'Status',
      minWidth: '160px',
      selector: 'fixture_status',
      sortable: true,
      cell: (row) => (
        <Badge className="text-capitalize" color={statusObj[row.fixture_status]} pill>
          {row.fixture_status === 1 ? "Live" : row.fixture_status === 2 ? 'Upcoming ' : row.fixture_status === 3 ? 'Completed' : row.fixture_status === 4 ? "Cancelled" : ''}
        </Badge>
      )

    },
    {
      name: 'Status',
      minWidth: '138px',
      selector: 'active',
      sortable: true,
      cell: (row) => (
        <div className="column-action">
          {
            row.fixture_status === 2 ?
              (
                <CustomInput
                  className='custom-control-success'
                  type='switch'
                  id={`success${row.id}`}
                  name='success'
                  onChange={() => SwitchClick(row.id)}
                  inline
                  defaultChecked={row.active !== 0}
                />
              ) : (
                <CustomInput
                  className='custom-control-danger'
                  type='switch'
                  id={`success${row.id}`}
                  name='success'
                  inline
                  defaultChecked
                  disabled
                />
              )
          }
        </div>
      )
    },
    {
      name: 'Active',
      minWidth: '138px',
      selector: 'active',
      sortable: true,
      cell: (row) => (
        <div className="column-action">
          {/* <a href={`/walls/edit/${row.feed_id}`}> */}
          <Edit width="17" className="cursor-pointer" title="Edit" onClick={() => editHandler(row)} />
          {/* </a> */}
          <Trash2 width="17" className="ml-2 cursor-pointer" title="Add Feed" onClick={() => handleConfirmText(row.id)} />
        </div>
      )
    }
  ]


  // ** Function in get data on page change
  const handlePagination = (page) => {
    setPaginate(paginate + 1)
    setCurrentPage(page.selected + 1)
  }

  // ** Function in get data on rows per page
  const handlePerPage = (e) => {
    const value = parseInt(e.currentTarget.value)
    setRowsPerPage(value)
    setCurrentPage(1)
  }

  // ** Function in get data on search query change
  const handleFilter = (val) => {
    setSearchTerm(val)
  }

  useEffect(() => {
    if (searchTerm) {
      const filterList = store.cricket_data.filter((userItem) => {

        if (userItem.player_type) {
          return userItem.player_type.toLowerCase().includes(searchTerm.toLowerCase())
        }
      })

      setListData(filterList)
      setCurrentPage(1)
    } else {
      setListData(store.cricket_data)
    }
  }, [searchTerm])

  if (store.isloading) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader
          blocking
        />
      </div>
    )
  }

  // ** Custom Pagination
  const CustomPagination = () => {
    const count = store.cricket_add_data?.data?.length > 0 ?  store.cricket_add_data?.last_page : store.cricketSearchFixture?.data?.length > 0 ? store.cricketSearchFixture?.last_page : store.cricket_page.last_page

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={
          'pagination react-paginate justify-content-end my-2 pr-1'
        }
      />
    )
  }

  console.log(store?.cricketSearchFixture?.data?.length)
  console.log(store?.cricketSearchFixture?.data?.length > 0 ? store.cricketSearchFixture?.last_page : store.cricket_page.last_page)
  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      q: searchTerm
    }
    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })
    if (store?.cricket_add_data?.data?.length >= 0) {
      return store?.cricket_add_data?.data
    } else if (store.cricketSearchFixture?.data?.length) {
      return store.cricketSearchFixture?.data
    } else if (listData.length === 0 && isFiltered) {
      return []
    }
  }


  const fixture = sto.fixture_name.map(function (fix) {
    return { value: fix.id, label: fix.fixture_name }
  })

  const filterid = sto.series_name.filter(s => s.series_name === serilist)
  const filterid2 = sto.fixture_name.filter(f => f.fixture_name === fixlist)
  const filterall = sto.fixture_name.map(f => f.fixture_name)

  useEffect(() => {
    dispatch(getAllFixture(serilist))
    console.log(sid)
  }, [serilist])

  const dateConverter = (startDate, timeEnd) => {
    const newStartDate = new Date(startDate)
    const newEndDate = new Date(timeEnd)
    const one_day = 1000 * 60 * 60 * 24
    let result = ''
    result = Math.ceil((newEndDate.getTime() - newStartDate.getTime()) / (one_day))
    console.log('date Converter result', result)
    if (result < 0) { return 0 }
    dispatch(getdaterange(result))
    return result
  }

  const idea = `{"series_id":${serilist}, "fixture_id":"${click ? fixlist : ""}", "page":${1}, "from":"${ccpicker}", "to":"${ccdateto}"}`
  const submit = (e) => {
    dateConverter(ccpicker, ccdateto)
    setVal(true)
    e.preventDefault()
    console.log(filterall.map(f => f === fixlist))
    setCurrentPage(1)
    //  const idea = `{"series_id":${serilist}, "fixture_id":"${fixlist}", "page":${currentPage}}`
    console.log(fixlist)
    dispatch(getCricketFixtureadd(idea))
    dispatch(collectseriesid(serilist))
    dispatch(collectfixtureid(fixlist))
  }

  useEffect(() => {
    if (store?.cricket_add_data.length !== 0) {
      setListData(store?.cricket_add_data)
    } else {
      if (store?.cricket_add_data.length === 0 && val) {
        setListData(store?.cricket_add_data)
      } else {
        setListData(store?.cricket_data)
      }
    }
  }, [submit])
  
  const serieslist = (e) => {
    setSerilist(e?.value)
    setRefresh(true)
    dispatch(getval(false))
    // dispatch(getAllFixture(filterid[0]?.id))
    setSid(filterid[0]?.id)
    console.log(filterid[0]?.id)
    setClick(false)
    // return store.series_name.filter(s => s.series_name === list)
  }

  const fixturelist = (e) => {
    const datas = e.map(e => e.value)
    setFixlist(datas)
    setClick(true)
    dispatch(getval(true))
    setRefresh(false)
    console.log(e.map(e => e.value))
    setFid(filterid2[0]?.id)
    console.log(filterid2[0]?.id)
    console.log(fid)
    // return store.series_name.filter(s => s.series_name === list)
  }

  const handleKeyPress = (e) => {
    if (e.keyCode === 13) {
      setCurrentPage(1)
      dispatch(getCricketFixtureadd(idea))
      dispatch(collectseriesid(serilist))
      dispatch(collectfixtureid(fixlist))
    }
  }

  useEffect(() => {
    const statusCode = searchStatus === 'Live' ? 1 : searchStatus === 'Upcoming' ? 2 : searchStatus === 'Completed' ? 3 : searchStatus === 'Cancelled' ? 4 : ''
    dispatch(searchCicketFixture(currentPage, searchType, statusCode))
  }, [searchType, searchStatus, currentPage])

  console.log(currentPage)
  console.log(searchStatus)

  const typeHandler = (value) => {
    setSearchType(value)
    setCurrentPage(1)
  }

  const statusHandler = (value) => {
    setSearchStatus(value)
    setCurrentPage(1)
  }

   // ************************************************************************************* //

   const options = []
   for (let i = 0; i < store1.cricket_total?.data?.total; i++) {
     if (page === store1.cricket_total?.data?.current_page) {
       options.push({
         value: `${store1.cricket_total?.data?.data[i]?.id}`,
         label: `${store1.cricket_total?.data?.data[i]?.series_name}`
       })
     }
     
   }
   const sleep = ms => new Promise(resolve => { setTimeout(() => { resolve() }, ms) })
   const optionsPerPage = 10
   
   const loadOptions = async (search, prevOptions, page) => {
     await sleep(1000)
     if (page === store1.cricket_total?.data?.current_page) {
       const nextData = store1.cricket_total?.data?.data?.map((nextData) => { return { value: nextData.id, label: nextData.series_name } })
       const scrollLength = [...prevOptions, ...nextData]
       let filteredOptions
       if (!search) {
         filteredOptions = scrollLength.length > 10 ? scrollLength : options
       } else {
         const searchLower = search.toLowerCase()
         filteredOptions = [...scrollLength.length > 10 ? scrollLength : options].filter(({ label }) => label.toLowerCase().includes(searchLower)
         )
       }
   
       const hasMore = Math.ceil(store1.cricket_total?.data?.total / optionsPerPage) > page
       const slicedOptions = filteredOptions.slice(
         (page - 1) * optionsPerPage,
         page * optionsPerPage
       )
   
       return {
         options: slicedOptions,
         hasMore
       }
     }
   }
   const defaultAdditional = {
     page: 1
   }
   
   const loadPageOptions = async (q, prevOptions, { page }) => {
     setPage(page)
     const { options, hasMore } = await loadOptions(q, prevOptions, page)
     return {
       options:[...options],
       hasMore,
   
       additional: {
         page: page + 1
       }
     }
   }
   
    // ************************************************************************************* //

  const data = [
    {
      title: <div><Filter size={18} /> Filter...</div>,
      content: (
        <>
           <Row>
         <Col >       
            <Label className="mb-0">From:</Label>         
               <Controller
              control={control}
              render={({ field: { ...field } }) => (
                <Flatpickr
                  {...field}
                  value={store.cric_from} 
                  onChange={(date) => edittime(date)}
                  options = {{
                    dateFormat: "Y-m-d"
                }}
                id='range-picker'
                  className={classnames('form-control', { 'is-invalid':  (ccpicker === "" && val) || (ccpicker === 'NaN-NaN-NaN' && val) })}
                  />
                  )}
                  name='reactSelect'
                  />
                  {(ccpicker === "" && val) || (ccpicker === 'NaN-NaN-NaN' && val) || (ccdateto === "" && val) || (ccdateto === 'NaN-NaN-NaN' && val) ?  <h5 className='text-danger ml-3'>Please select a date</h5> : ""}
            </Col>
         &emsp;&nbsp;            
         <Col>
            <Label className="mb-0">To:</Label>&nbsp;&nbsp;   
             <Controller
              control={control}
              render={({ field: { ...field } }) => (
                <Flatpickr
                  {...field}
                  value={store.cric_to} 
                  onChange={dat => edit(dat)}
                  isClearable={false}
                  options = {{
                    dateFormat: "Y-m-d"
                }}
                id='range-picker'
                  className={classnames('form-control', { 'is-invalid':  (ccdateto === "" && val) || (ccdateto === 'NaN-NaN-NaN' && val) })}
                  />
                  )}
                  name='reactSelect'
            />
             &emsp;&nbsp;
        </Col>
       </Row>
          <Row>
          <Col className='mb-1' md='6' sm='12' >
            <Label>series</Label>
              <AsyncPaginate
              menuPortalTarget={document.querySelector('body')}
                theme={selectThemeColors}
                className={classnames('react-select', { 'is-invalid': serilist === "" && val })}
                classNamePrefix='select'
                additional={defaultAdditional}
                value={value?.label}
                loadOptions={loadPageOptions}
                onChange={(e) => serieslist(e)}
                onKeyDown={(e) => handleKeyPress(e)}
              />
            {serilist === "" && val ?  <h5 className='text-danger'>Please enter a value</h5> : ""}
            </Col>
            <Col className='mb-1' md='6' sm='12'>
              <Label>fixture</Label>
              <Select
                menuPortalTarget={document.querySelector('body')}
                onChange={(e) => fixturelist(e)}
                theme={selectThemeColors}
                value={refresh ? "" : fid}
                className='react-select'
                classNamePrefix='select'
                isMulti
                // defaultValue={fixture[0]}
                options={fixture ? fixture : ""}
                placeholder='Select cricket fixture'
                isClearable={true}
              />
            </Col>
          </Row>        
          <Row>
            <Col className='mb-1 mt-1' md='6' sm='12'>
              <Label>Search:</Label>
              <Select
                menuPortalTarget={document.querySelector('body')}
                onChange={(e) => typeHandler(e?.label)}
                theme={selectThemeColors}
                className='react-select'
                classNamePrefix='select'
                options={typeOptions}
                placeholder='search...'
                isClearable={true}
              />
            </Col>
            <Col className='mb-1 mt-1' md='6' sm='12'>
              <Label>Search:</Label>
              <Select
                menuPortalTarget={document.querySelector('body')}
                onChange={(e) => statusHandler(e?.label)}
                theme={selectThemeColors}
                className='react-select'
                classNamePrefix='select'
                options={statusOptions}
                placeholder='search...'
                isClearable={true}
              />
            </Col>
          </Row>
          <Button.Ripple
            color='primary'
            onClick={(e) => handleSubmit(submit(e))}
          >submit</Button.Ripple>
        </>
      )
    }
  ]
  return (
    <Fragment>
      <Breadcrumbs breadCrumbTitle='Cricket Fixture' breadCrumbActive='List' />
      <AppCollapse data={data} type='border' accordion className="mb-1" />
      <Card>
        <Row className='ml-1 mt-1'>
        <Button.Ripple 
                color='primary'
                disabled={store.date_range > 90}
                onClick ={() => download()}>      
                {store.download_timer ?  
                <>
                <Spinner color='white' size='sm' />
                <span className='ml-50'>Loading...</span>
                </> : 'Download CSV' }
      </Button.Ripple>
      <Button.Ripple
                outline id='controlledPopover'
                color='flat-info'
                >
                  <HelpCircle />
                </Button.Ripple>
      <Popover
        placement='top'
        target='controlledPopover'
        isOpen={popoverOpen}
        toggle={() => setPopoverOpen(!popoverOpen)}
      >
        <PopoverHeader>INFO</PopoverHeader>
        <PopoverBody>
                  pick a date less than 90 days to download
        </PopoverBody>
      </Popover>
      </Row>
        <DataTable
          noHeader
          pagination
          subHeader
          responsive
          paginationServer
          columns={columns}
          paginationPerPage
          paginationDefaultPage={currentPage}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          paginationComponent={CustomPagination}
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              handlePerPage={handlePerPage}
              rowsPerPage={rowsPerPage}
              searchTerm={searchTerm}
              currentPage={currentPage}
              handleFilter={handleFilter}
              setListData={setListData}
              cricket_data={store.cricket_data}
              cricket_add_data={store.cricket_add_data}
              setCurrentPage={setCurrentPage}
            />
          }
        />
      </Card>
    </Fragment>
  )
}

export default UsersList